  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAADCAYAAABWKLW/AAAABGdBTUEAALGPC/xhBQAAABJJREFUCB1jYECA/wgmlAUXAQAkAwH/DVk11gAAAABJRU5ErkJggg==);
